.list {
  margin-bottom: 48px; }
  .list__item {
    position: relative;
    padding-left: 40px;
    font-size: 20px;
    line-height: 30px; }
    @media screen and (max-width: 767px) {
      .list__item {
        font-size: 16px;
        line-height: 24px; } }
    .list__item:not(:last-child) {
      margin-bottom: 16px; }
    .list__item:before {
      content: "";
      position: absolute;
      top: 8px;
      left: 0;
      width: 15px;
      height: 15px;
      background-color: #f41a2a;
      border-radius: 50%; }

.icon {
  fill: #f41a2a; }

.burger {
  display: none;
  position: relative;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border-radius: 50%;
  flex-shrink: 0;
  border: 2px solid #ffffff; }
  @media screen and (max-width: 767px) {
    .burger {
      display: block;
      margin-right: 10px; } }

.burger__strip,
.burger__strip:before,
.burger__strip:after {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -1px;
  margin-left: -10px;
  width: 20px;
  height: 2px;
  background-color: #ffffff; }

.burger__strip:before,
.burger__strip:after {
  content: "";
  transition: 0.3s; }

.burger__strip:before {
  transform: translateY(-5px); }

.burger__strip:after {
  transform: translateY(5px); }

.burger--active .burger__strip {
  height: 0; }

.burger--active .burger__strip:before {
  transform: rotate(45deg); }

.burger--active .burger__strip:after {
  transform: rotate(-45deg); }

